<template>
  <div class="modal-box">
    <div class="modal-title">
      <h3>
        <base-icon
          iconRef="person"
          position="left"
        />Nieuwe Klant
      </h3>
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="closeModal"
      >
        <base-icon iconRef="close" />
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Basisgegevens</h4>
        </div>
        <div class="input-container">
          <label
            for="clientName"
            class="label required"
          >Klantnaam</label>
          <base-input
            type="text"
            id="clientName"
            name="clientName"
            placeholder="Klantnaam"
            v-model="newClientData.clientName"
            data-vv-as="Klantnaam"
            v-validate="'required'"
          />
          <base-input-warning :error="errors.first('clientName')" />
        </div>
        <div class="input-container">
          <label
            for="clientDescription"
            class="label"
          >Beschrijving</label>
          <textarea
            id="clientDescription"
            class="input"
            rows="4"
            v-model="newClientData.description"
          ></textarea>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Contactpersoon</h4>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label
              class="label required"
              for="contactFirstName"
            >Voornaam</label>
            <base-input
              type="text"
              id="contactFirstName"
              name="contactFirstName"
              placeholder="Voornaam"
              v-model="newClientData.contact.firstName"
              data-vv-as="Voornaam"
              v-validate="'required'"
            />
            <base-input-warning :error="errors.first('contactFirstName')" />
          </div>
          <div class="input-container">
            <label
              class="label required"
              for="contactLastName"
            >Achternaam</label>
            <base-input
              type="text"
              id="contactLastName"
              name="contactLastName"
              placeholder="Achternaam"
              v-model="newClientData.contact.lastName"
              data-vv-as="Achternaam"
              v-validate="'required'"
            />
            <base-input-warning :error="errors.first('contactLastName')" />
          </div>
        </div>
        <div class="input-container">
          <label class="label">E-mail</label>
          <base-input
            type="email"
            placeholder="Email"
            v-model="newClientData.contact.email"
          />
        </div>
        <div class="input-container">
          <label class="label">Telefoonnummer</label>
          <base-input
            type="tel"
            placeholder="0612345678"
            v-model="newClientData.contact.tel"
          />
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <div class="flex-divider"></div>
      <base-button
        @click="closeModal"
        type="secondary"
      >Annuleren</base-button>
      <base-button
        @click="createClient"
        :loading="creatingClient"
        :disabled="creatingClient"
      >
        <base-icon
          iconRef="checkmark"
          position="left"
        />Aanmaken
      </base-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "client-create-modal",
  data: function() {
    return {
      creatingClient: false,
      newClientData: {
        clientName: "",
        description: "",
        contact: {
          firstName: "",
          lastName: "",
          email: "",
          tel: ""
        }
      }
    };
  },
  methods: {
    createClient: function() {
      this.$validator.validate().then(result => {
        if (!result) {
        } else {
          this.creatingClient = true;
          const newClient = this.$createNewClient(this.newClientData);
          this.$store.dispatch("createClient", newClient).then(() => {
            this.closeModal();
            this.creatingClient = false;
          });
        }
      });
    },
    closeModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "client",
        modalType: "create",
        shown: false
      });
    }
  }
};
</script>



