<template>
  <div class="modal-box">
    <div class="modal-title">
      <h3>
        <base-icon
          iconRef="activity"
          position="left"
        />Nieuwe Meting
      </h3>
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="closeModal"
      >
        <base-icon iconRef="close" />
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Basisgegevens</h4>
        </div>
        <div class="input-container">
          <label class="label">Metingtype</label>
          <select
            class="input"
            v-bind:value="newMeasurementData.type"
            @change="newMeasurementData.type = $event.target.value"
            v-validate="'required'"
            data-vv-as="Metingtype"
            name="measurementType"
          >
            <option value="Nulmeting">Nulmeting</option>
            <option value="Tussenmeting">Tussenmeting</option>
            <option value="Eindmeting">Eindmeting</option>
          </select>
          <base-input-warning :error="errors.first('measurementType')" />
        </div>
        <div class="input-container">
          <label class="label">Label</label>
          <base-input
            type="text"
            placeholder="Label"
            v-model="newMeasurementData.label"
          />
        </div>
        <div class="input-container">
          <label
            for
            class="label"
          >Beschrijving</label>
          <textarea
            class="input"
            rows="4"
            placeholder="Beschrijving"
            v-model="newMeasurementData.description"
          ></textarea>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label required">Meting door</label>
            <select
              class="input"
              name="author"
              id="author"
              v-model="newMeasurementData.author"
              data-vv-as="Meting door"
              v-validate="'required'"
            >
              <option
                :value="username"
                v-for="username in usersNames"
                :key="username"
              >{{username}}</option>
            </select>
            <base-input-warning :error="errors.first('author')" />
          </div>
          <div class="input-container">
            <label
              class="label required"
              for="date"
            >Datum</label>
            <base-datepicker v-model="newMeasurementData.date" />
          </div>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Meetwaarden</h4>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label
              class="label required"
              for="hz125"
            >125 Hz</label>
            <base-input
              id="hz125"
              name="hz125"
              data-vv-as="125 Hz"
              v-validate="'required'"
              type="number"
              placeholder="0"
              v-model="newMeasurementData.hz125"
            />
            <base-input-warning :error="errors.first('hz125')" />
          </div>
          <div class="input-container">
            <label
              class="label required"
              for="hz250"
            >250 Hz</label>
            <base-input
              id="hz250"
              name="hz250"
              data-vv-as="250 Hz"
              v-validate="'required'"
              type="number"
              placeholder="0"
              v-model="newMeasurementData.hz250"
            />
            <base-input-warning :error="errors.first('hz250')" />
          </div>
          <div class="input-container">
            <label
              class="label required"
              for="hz500"
            >500 Hz</label>
            <base-input
              id="hz500"
              name="hz500"
              data-vv-as="500 Hz"
              v-validate="'required'"
              type="number"
              placeholder="0"
              v-model="newMeasurementData.hz500"
            />
            <base-input-warning :error="errors.first('hz500')" />
          </div>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label
              class="label required"
              for="hz1000"
            >1000 Hz</label>
            <base-input
              id="hz1000"
              name="hz1000"
              data-vv-as="1000 Hz"
              v-validate="'required'"
              type="number"
              placeholder="0"
              v-model="newMeasurementData.hz1000"
            />
            <base-input-warning :error="errors.first('hz1000')" />
          </div>
          <div class="input-container">
            <label
              class="label required"
              for="hz2000"
            >2000 Hz</label>
            <base-input
              id="hz2000"
              name="hz2000"
              data-vv-as="2000 Hz"
              v-validate="'required'"
              type="number"
              placeholder="0"
              v-model="newMeasurementData.hz2000"
            />
            <base-input-warning :error="errors.first('hz2000')" />
          </div>
          <div class="input-container">
            <label
              class="label required"
              for="hz4000"
            >4000 Hz</label>
            <base-input
              id="hz4000"
              name="hz4000"
              data-vv-as="4000 Hz"
              v-validate="'required'"
              type="number"
              placeholder="0"
              v-model="newMeasurementData.hz4000"
            />
            <base-input-warning :error="errors.first('hz4000')" />
          </div>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <div class="flex-divider"></div>
      <base-button
        @click="closeModal"
        type="secondary"
      >Annuleren</base-button>
      <base-button
        @click="createMeasurement"
        :loading="creatingMeasurement"
        :disabled="creatingMeasurement"
      >
        <base-icon
          iconRef="checkmark"
          position="left"
        />Aanmaken
      </base-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "room-create-modal",
  data: function() {
    return {
      creatingMeasurement: false,
      newMeasurementData: {
        measurementName: "",
        description: "",
        type: "",
        label: "",
        author: "",
        date: Date.now(),
        hz125: "",
        hz250: "",
        hz500: "",
        hz1000: "",
        hz2000: "",
        hz4000: ""
      }
    };
  },
  computed: {
    usersList: function() {
      return this.$store.getters.getUsersList;
    },
    usersNames: function() {
      const usersNames = [];
      this.usersList
        .filter(user => {
          return user.data().doesMeasurements;
        })
        .forEach(user => {
          usersNames.push(user.data().name);
        });
      return usersNames;
    }
  },
  methods: {
    createMeasurement: function() {
      this.$validator.validate().then(result => {
        if (!result) {
        } else {
          this.creatingMeasurement = true;
          const newMeasurement = this.$createNewMeasurement(
            this.newMeasurementData
          );
          newMeasurement.belongsTo = this.$route.params.roomId;
          this.$store.dispatch("createMeasurement", newMeasurement).then(() => {
            this.closeModal();
            this.creatingMeasurement = false;
          });
        }
      });
    },
    closeModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "measurement",
        modalType: "create",
        shown: false
      });
    }
  }
};
</script>



